.App {
  height: 100vh;
  white-space: nowrap;
  color: #fcfcfc;
  overflow: hidden;
}

.inline-block {
  display: inline-block;
  height: 100vh;
  vertical-align: top;
  white-space: normal;
}

.sidebar {
  width: 22vw;
  background-color: rgb(218, 46, 117);
}

.sidebar ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.sidebar ul li {
  padding: 1em;
  cursor: pointer;
}

.sidebar ul a {
  color: #fcfcfc;
  text-decoration: none;
}

.sidebar ul li:hover {
  background-color: rgb(204, 34, 104);
}

.mainarea {
  width: 78vw;
  background-color: rgb(9, 130, 154);
  background-color: #fcfcfc;
  color: #2e2e2e;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.mainarea .section{
   padding: 1.3em 1em;
   padding-bottom: 0;
}

.profile {
  text-align: center;
  /*height: 30vh;*/
  height: 220px;
}

.mugshot{
    border: 2px solid;
    width: 120px;
    margin: auto;
    margin-top: 15px;
    margin-bottom: 15px;
    border-radius: 2px;
}

.capsule{
    background: #FFEB3B;
    color: #2e2e2e;
    padding: 1px 3px;
    border-radius: 6px;
    font-size: 0.8em;
    font-weight: bold;
}

.section {
  height: calc(100vh - 20px - 2em);
}

.footer {
  /*background-color: rgb(240, 240, 240);
  color: #222;*/
  /*padding: 1em;*/
  line-height: 2.4;
  /*padding-left: 0;
  padding-right: 0;*/

    background-color: rgba(0, 0, 0, 0.68);
    color: #efefef;
}

.contact-form{
   margin-left: 15px;
   margin-right: 15px;
}

.social{
  float: right;
  margin-right: 20px;
}

.social a.fa {
    background: #efefef;
    border: 1px solid;
    border-radius: 3px;
    padding: 0.2em;
    text-decoration: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: #da2e75;
    margin-right: 5px;
}

.aboutme{
    font-size: 1.2em;
    line-height: 1.4;
}

.card {
    width: 300px;
    height: 150px;
    display: inline-block;
    -webkit-box-shadow: 1px 1px 1px 2px #505050;
            box-shadow: 1px 1px 1px 2px #505050;
    margin: 10px;
    position: relative;
}

.card img {
    width: 100%;
    height: 100%;
    opacity: 0.9;
}

p.card-desc {
    position: absolute;
    bottom: -1em;
    width: 100%;
    background: #505050;
    color: #fcfcfc;
    padding: 2px 4px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}



